/* Forms */

.v5-fieldset {
padding: 10px;
width: 100%;
float: left;
background: $gray;
margin-bottom: 20px;
}

.v5-legend {
float: left;
font-size: 1.5em !important;
margin-bottom: 10px;
padding: 0;
}

.v5-legend + p {
clear: both;
}

.v5-radio-button-container {
background: #333333;
float: left;
color: white;
margin-right: 10px;
margin-bottom: 10px;
}

.v5-button {
float: left;
margin: 10px 10px 10px 0;
background: $red;
color: white;
}

.v5-button:focus {
outline: #ffdd00 5px solid !important;
}

button.v5-button {
padding: 10px;
border: 0;
}

.v5-button:hover {
cursor: pointer;
background: $hseblack;
}

.v5-button a {
float: left;
background: $red;
padding: 10px;
color: white;
text-decoration: none;
}

.v5-label {
float: left;
padding: 10px;
}


.v5-radio {
margin-right: 10px;
position: relative;
top: 2px;
}

.v5-radio-button-container label span {
position: relative;
top: 0;
}

.v5-radio-button-container label:hover {
cursor: pointer !important;
}

input[type=radio] {
float: left;  
width: auto;
display: inline-block;
}

.radio-toolbar {
float: left;
margin-right: 10px;
}

.radio-toolbar input[type="radio"] {
opacity: 0;
visibility: visible !important;
width: 0;
margin-right: 5px;
}


.radio-toolbar .v5-label-on {
border-right: 0 solid $darkgray;	
}

.radio-toolbar .v5-label-off {
border-left: 0 solid $darkgray;		
}

.radio-toolbar label {
display: inline-block;
background-color: $gray;
padding: 5px 10px;
font-family: sans-serif, Arial;
color: black;
outline: 2px solid $darkgray;
}

.radio-toolbar label:hover {
cursor: pointer;
}

.radio-toolbar input[type="radio"]:focus + label {
outline: 3px solid $focus;
}

.radio-toolbar input[type="radio"]:checked + label {
background-color: $red;
color: white;
}

.radio-toolbar  input.v5-input-on[type="radio"]:checked + label {
background-color: $yes;
color: white;
}

